:global {
  body[data-theme='light'] {
    --search-bg: var(--white);
    --search-color: var(--gray-300);
    --search-placeholder-color: var(--gray-200);
    --search-shadow: var(--shadow)
  }

  body[data-theme='dark'] {
    --search-bg: var(--gray-400);
    --search-color: var(--white);
    --search-placeholder-color: var(--white);
    --search-shadow: unset
  }
}

.search {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 10px 10px 10px 16px;
  background-color: var(--search-bg, white);
  display: flex;
  align-items: center;
  border-radius: 15px;
  box-shadow: var(--search-shadow);

  @media (min-width: 767px) {
    padding: 10px 10px 10px 30px;
  }

  & > button {
    flex-shrink: 0;
  }

  .label {
    cursor: pointer;
  }

  .textField {
    padding: 0 10px;
    flex-grow: 5;
    height: 60px;
    max-width: calc(100% - 100px - 20px);
    border: none;
    color: var(---search-color);
    font-size: 0.75rem;
    line-height: 1.5;
    cursor: pointer;
    background-color: var(--search-bg);

    &::placeholder {
      color: var(--search-placeholder-color);
    }

    &:focus {
      outline: none;
    }

    @media (min-width: 767px) {
      margin: 0 5px;
      font-size: 1.25rem;
    }
  }
}

.error {
  position: absolute;
  right: 50%;
  bottom: 5px;
  color: var(--error-color);
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 700;

  @media (min-width: 767px) {
    right: 120px;
    top: 50%;
    transform: translateY(-50%);
    bottom: unset;
  }
}